@import url(https://cdnjs.cloudflare.com/ajax/libs/font-awesome/4.7.0/css/font-awesome.min.css);
::-webkit-scrollbar{
    width: 5px;
    height: 6px;
  }
  
  ::-webkit-scrollbar-track{
    box-shadow: inset 0 0 5px #a5aaad;
    border-radius: 10px;
  }
  
  ::-webkit-scrollbar-thumb{
    background: #3ea175;
    border-radius: 10px;
  }
  
  ::-webkit-scrollbar-thumb:hover{
    background: #a5aaad;
  }
  
  *{
    margin: 0;
    padding: 0;
  }
  
  body{
    box-sizing: border-box;
  }

  
.text-primary-p{
    color: #a5aaad;
    font-size: 20px;
    font-weight: 700;
}

.font-bold{
    font-weight: 700;
}

.font-title{
    color: #2e4a66;
}

.text-lightblue{
    color: #469cac;
}

.text-red{
    color: #cc3d38;
}

.text-yellow{
    color: #a98921;
}

.text-green{
    color: #3b9668;
}

.container{
    display: grid;
    height: 100vh;
    grid-template-columns: 0.8fr 1fr 1fr 1fr;
    grid-template-rows: 0.2fr 3fr;
    grid-template-areas: 
    "sidebar nav nav nav"
    "sidebar main main main";
  }
  
  @media only screen and (max-width:978px) {
    .container{
      grid-template-columns: 1fr;
      grid-template-rows: 0.2fr 3fr;
      grid-template-areas: 
      "nav"
      "main";
    }
  }

.navbar {
    background-color: #FFF;
    grid-area: nav;
    display: flex;
    height: 60px;
    align-items: center;
    justify-content: space-between;
    padding: 0 30px;
    border-bottom: 1px solid lightgray;
}

.navbar .nav-icon{
    display: none;
}

.navbar .nav-icon > i{
    font-size: 26px;
    color: #a5aaad;
}

.navbar .navbar-left > a{
    margin-right: 30px;
    text-decoration: none;
    color: #a5aaad;
    font-size: 15px;
    font-weight: 700;
}

.navbar .navbar-left .active-link{
    color: #265acc;
    border-bottom: 3px solid #265acc;
    padding-bottom: 12px;
}

.navbar .navbar-right{
    display: flex;
    justify-content: center;
    align-items: center;
}

.navbar .navbar-right > a{
    margin-left: 20px;
    text-decoration: none;
}

.navbar .navbar-right > a > i{
    color: #265acc;
    font-size: 16px;
    border-radius: 50px;
    background-color: #FFF;
    box-shadow: 2px 2px 5px #d9d9d9, -2px -2px 5px #FFF;
    padding: 7px;
}

@media only screen and (max-width: 978px){
    .navbar .nav-icon {
        display: inline;
        cursor: pointer;
    }
    .navbar .navbar-left > a{
        font-size: 13px;
    }
}

@media only screen and (max-width: 480px){
    .navbar-left{
        display: none;
    }
}
#sidebar{
    background: #020509;
    grid-area: sidebar;
    overflow-y: auto;
    padding: 20px;
    height: 100vh;
    width: 18%;
    position: fixed;
}

.sidebar-title{
    display: flex;
    justify-content: space-between;
    align-items: center;
    color: #f3f4f6;
    margin-bottom: 30px;
}

.sidebar-title > h1{
    font-size: 20px;
    display: inline;
}

.sidebar-title > i{
    font-size: 18px;
    margin: 0 10px;
    display: none;
}

.sidebar-menu > h2{
    color: #3ea175;
    font-size: 18px;
    margin: 25px 0 15px;
    padding: 0 10px;
    font-weight: 800;
}

.sidebar-link{
    color: #f3f4f6;
    margin-bottom: 5px;
    padding: 12px 0px;
}

.active-menu-link{
    background: rgba(62, 161, 112, 0.3);
    color: #3ea175 !important;
    padding: 10px 7px;
    border-radius: 3px;
    margin-bottom: 5px;
    transition: all .5 ease-in-out;
}

.sidebar-link > .link{
    text-decoration: none;
    color: #a5aaad;
    font-weight: 700;
}

.sidebar-link .link i{
    margin-right: 10px;
    font-size: 18px;
}

.sidebar-logout{
    margin-top: 20px;
    padding: 10px;
    color: #e65061;
}

.sidebar-logout > a{
    text-decoration: none;
    color: #e65061;
    font-weight: 700;
    text-transform: uppercase;
}

.sidebar-logout > i{
    margin-right: 10px;
    font-size: 18px;
}

.sidebar-responsive{
    display: inline !important;
    margin: 0 !important;
    z-index: 9999 !important;
    left: 0 !important;
    position: absolute;
}

@media only screen and (max-width: 978px) {
    #sidebar{
        position: fixed;
        margin: 0 0 0 -100%;
        transition: all 1s ease-in-out;
        width: 90%;
    }

    .sidebar-title > i{
        display: inline;
        cursor: pointer;
    }
}
main{
    background: #f3f4f6;
    grid-area: main;
    overflow-y: auto;
}

.main-container{
    padding: 20px 35px;
}

.main-title{
    display: flex;
    align-items: center;
}

.main-greeting > h1{
    font-size: 24px;
    color: #2e4a66;
    margin-bottom: 5px;
}

.main-greeting > p{
    font-size: 14px;
    font-weight: 700;
    color: #a5aaad;
}

.main-cards{
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    grid-gap: 30px;
    gap: 30px;
    margin: 20px 0;
}

.card{
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    height: 70px;
    padding: 25px;
    border-radius: 5px;
    background: #fff;
    box-shadow: 5px 5px 13px #ededed, -5px -5px 13px #fff;
}

.card-inner{
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-top: 14px;
}

.card-inner > span{
    font-size: 25px;
}

.charts{
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-gap: 30px;
    gap: 30px;
    margin-top: 50px;
}

.charts-left,
.charts-right{
    padding: 25px;
    border-radius: 5px;
    background: #FFF;
    box-shadow: 5px 5px 13px #ededed, -5px -5px 13px #fff;
}

.charts-left-title,
.charts-right-title{
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.charts-left-title > div > h1,
.charts-right-title > div > h1{
    font-size: 24px;
    color: #2e4a55;
    margin-bottom: 5px;
}

.charts-left-title > i,
.charts-right-title > i{
    color: #FFF;
    font-size: 20px;
    border-radius: 5px;
    padding: 15px;
    border: none;
}

.charts-left-title > i{
    background: #ffc100;
}

.charts-right-title > i{
    background: #39447a;
    border-radius: 200px 0px 200px 200px;
}

.charts-right-cards {
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-gap: 20px;
    gap: 20px;
    margin-top: 30px;
}

.card1, .card2, .card3, .card4{
    text-align: center;
    padding: 25px;
    border-radius: 5px;
    font-size: 14px;
}

.card1{
    background: #d1ecf1;
    color: #35a4ba;
}

.card2{
    background: #d2f9ee;
    color: #38e1b0;
}

.card3{
    background: #d6d6d6;
    color: #3a3e41;
}

.card4{
    background: #fddcdf;
    color: #f65a6f;
}

@media only screen and (max-width: 885px) {
    .main-cards{
        grid-template-columns: 1fr;
        grid-gap: 10px;
        gap: 10px;
        margin-bottom: 0;
    }

    .charts{
        grid-template-columns: 1fr;
        margin-top: 30px;
    }

}
.signInContainer{
    display: flex;
    flex-direction: row;
    color: #2861A3;
    height: 100vh;
    width: 100vw;
    background-color: #DDE3E7;
    justify-content: center;
    align-items: center;
}

.signInContainer .form{
    text-align: center;
    width: 30%;
    border-right: 2px solid #EEE;
    padding: 10px 40px 10px;
}

.signInContainer .form .inputBox{
    position: relative;
    width: 100%;
    margin-top: 10px;
}

.signInContainer .form input{
    display: block;
    width: 100%;
    padding: 2.5% 5px;
    margin: 7% 0;
    border-radius: 5px;
    border: none;
    outline: none;
    font-size: 16px;
}

.signInContainer .form input[type="submit"]{
    color: #EEE;
    font-size: 22px;
    background-color: #2861A3;
    transition: .3s ease-in-out;
    cursor: pointer;
}

.signInContainer .form input[type="submit"]:hover{
    background-color: #e91e63;
    transition: .3s ease-in-out;
}

.signInContainer .form span{
    position: absolute;
    left: 0;
    top: 0;
    margin: 6px 5px;
    padding: 5px 0;
    font-size: 16px;
    pointer-events: none;
    transition: .2s;
    color: #666;
}

.signInContainer .form input:valid ~ span,
.signInContainer .form input:focus ~ span{
    color: #e91e63;
    font-weight: 600;
    font-size: 14px;
    transform: translateY(-110%);
    transition: .4s;
}

.signInContainer .imgContainer{
    display: flex;
    width: 40%;
}

.signInContainer .imgContainer img{
    width: 100%;
}

@media (max-width: 991px){

    .signInContainer{
        flex-direction: column-reverse;
        height: 100vh;
    }

    .signInContainer .form{
        border-right: none;
        width: 80%;
    }

    .signInContainer .imgContainer{
        width: 40%;
    }
}

@media (max-width: 500px){

    .signInContainer .form{
        border-right: none;
        width: 75%;
    }

    .signInContainer .form input{
        margin: 10% 0;
    }

    .signInContainer .imgContainer{
        width: 40%;
    }

}
.card-container{
    padding: 20px;
    margin: 10px;
    display: flex;
    justify-content: space-between;
    background-color: rgba(1, 18, 26, 0.11);
    border-radius: 12px;
    box-shadow: 5px 5px 13px #ededed, -5px -5px 13px #fff;
}

.card-container span{
    color: rgba(0, 0, 0, 0.603);
}

.card-container span{
    width: 20%;
    text-transform: capitalize;
}

.card-container span>a{
    text-decoration: none;
    color: rgba(0, 0, 0, 0.8);
}

.card-container span>a:hover{
    text-decoration: underline;
}

.card-btn{
    padding: 5px;
    min-width: 12%;
    cursor: pointer;
    border-radius: 5px;
    outline: none;
    text-transform: uppercase;
    max-height: 30px;
    margin: 0 15px;
}

.card-btn.edit{
    color: #EEE;
    background-color: #115384;
    border: 1px solid #115384;
}

.card-btn.edit:hover{
    color: #115384;
    background-color: transparent;
}

.card-btn.delete{
    color: #EEE;
    background-color: #BA372A;
    border: 1px solid #BA372A;
}

.card-btn.delete:hover{
    color: #BA372A;
    background-color: transparent;
}

@media only screen and (max-width: 800px){
    .card-container{
        flex-direction: column;
    }

    .card-container span{
        margin: 2% 1% 3%;
        width: 90%;
    }

    .btn-container{
        display: flex;
        justify-content: space-between;
        margin-top: 4%;
    }

    .card-btn{
        margin: 2% 5% 2% 0;
        width: 30%;
    }

}
.bm-container{
    grid-area: main;
    padding: 25px;
}

.bm-container .bm-header{
    display: flex;
    justify-content: space-between;
}

.bm-container .btn-add{
    text-transform: uppercase;
    border: 1px solid;
    outline: none;
    padding: 10px 15px;
    font-size: 15.5px;
    font-weight: 700;
    cursor: pointer;
    border-radius: 50px;
    color: white;
    background-color: #8BC252;
}

.bm-container .btn-add:hover{
    background-color: transparent;
    color: #8BC252;
}

.bm-container input{
    display: inline-block;
    padding: 10px;
    border-radius: 50px;
    outline: none;
    border: 1px solid rgba(0, 0, 0, 0.377);
}

.bm-container .bm-header i{
    cursor: pointer;
    font-size: 22px;
    color: rgba(0, 0, 0, 0.418);
    padding: 10px;
}

.bm-container > hr{
    margin: 25px;
}

@media only screen and (max-width: 500px){

    .bm-container input{
        width: 60%;
    }
}
